	module.exports = {"data":{"markdownRemark":{"html":"<p>You must have noticed in <strong>main.controller.js</strong> how <em>things</em> were retrieved from the database and displayed:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$http.get('/api/things').success(function(awesomeThings){  \n    $scope.awesomeThings = awesomeThings;  \n});</code></pre>\n      </div>\n<p>What this does is call the api with a “get” request, which is then routed by <strong>/server/api/thing/index.js</strong> to the <em>exports.index</em> function in <strong>thing.controller.js</strong>. You’ll also notice in <strong>main.controller.js</strong> that there are included examples of <em>$http.post</em> and <em>$http.delete</em> functions too! How nice!</p>","fields":{"slug":"/miscellaneous/accessing-the-database-from-your-front-end/"},"frontmatter":{"title":"Accessing the Database from Your Front End"}}},"pathContext":{"slug":"/miscellaneous/accessing-the-database-from-your-front-end/"}}

